@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

body {
 height: 0;
}

.ant-layout {
  margin: 3% 12% 0% 12%;
  height: auto;
  font-family: 'Roboto', sans-serif;
  background-color: transparent;
}

.contenedor__botones
{
    display: flex;
    justify-content: center;
    background-color: beige;
    width: 100%;
}

@media (max-width: 1024px) {
  .ant-layout {
    margin: 3% 3% 0% 3%;
    padding: 0;
  }
}

p,
span {
  font-size: 16px;
  color: black;
  font-weight: 500;
}

.contenedor {
  margin: 0 2%;
}

.titulo {
  font-size: 30px;
  text-align: center;
  color: #ffffff;
}

.subtitulo {
  font-size: 20px;
  text-align: left;
}

.seccion {
  background-color: #ffffff;
  padding: 5px 20px;
  border: 2px solid black;
  border-color: #ffffff;
}

.margen__item {
  margin-top: 5px;
}

.margen {
  margin-top: 1rem;
}

.ant-radio-inner {
  width: 20px;
  height: 20px;
  background-color: white;
}

.ant-radio-inner::after {
  width: 12px;
  height: 12px;
}

div.ant-select-selector {
  border-radius: 5px !important;
  border: none !important;
}

.campo__input {
  border: 1px solid black;
  border-radius: 5px;
  outline: none;
}

.margen__izquierdo {
  margin-left: 10px;
}

.icon {
  cursor: pointer;
  position: relative;
  top: 4px;
  margin-left: 2rem;
  font-size: 24px;
  color: #999;
  transition: all 0.3s;
}

.icon:hover {
  color: #777;
}

.icon[disabled] {
  cursor: not-allowed;
  opacity: 0.5;
}

.submit {
  color: black;
  border: 1px solid black;
  border-radius: 5px;
  margin-top: 1rem;
}

.space {
  display: grid;
  grid-template-columns: 10% 5% 5% 10% 5% 10% 10% 10%;
  grid-column-gap: 3rem;
  -webkit-column-gap: 3rem;
          column-gap: 3rem;
}

.borrar-fila {
  cursor: pointer;
  border: none;
  outline: none;
  font-size: 12px;
  background-color: transparent;
  color: red;
  font-weight: 500;
}

.tope {
  margin-top: 5px;
}

.header
{
    background-color: #000000;
    padding: 5px;
    width: 100%;
}


@media screen and (max-width: 480px) {
  .space {
    grid-template-columns: 1fr;
  }

  .position-grid {
    grid-column: 1/2;
  }

  .margen__derecha {
    margin-right: 1rem;
    display: flex;
    align-items: center;
  }
}

